import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { MatCardModule, MatDialogModule } from '@angular/material';
import { MatToolbarModule} from '@angular/material/toolbar';
import { MatIconModule} from '@angular/material/icon';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AppComponent } from './app.component';
import { BooksLibaryComponent } from './components/books-libary/books-libary.component';

import { LibaryService } from './services/libary.service';
import { BookValidationService } from './services/book-validation.service';

import { AddBookPopUpComponent } from './pop-ups/add-book-pop-up/add-book-pop-up.component';
import { EditBookPopUpComponent } from './pop-ups/edit-book-pop-up/edit-book-pop-up.component';
import { DeleteBookPopUpComponent } from './pop-ups/delete-book-pop-up/delete-book-pop-up.component';
import { HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';

@NgModule({
  declarations: [
    AppComponent,
    BooksLibaryComponent,
    AddBookPopUpComponent,
    EditBookPopUpComponent,
    DeleteBookPopUpComponent
  ],
  imports: [
    BrowserModule,
    NgbModule.forRoot(),
    MatDialogModule,
    MatCardModule,
    MatIconModule,
    MatToolbarModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    HttpClientModule,
    HttpModule
  ],
  providers: [LibaryService, BookValidationService],
  entryComponents: [AddBookPopUpComponent,EditBookPopUpComponent,DeleteBookPopUpComponent], 
  bootstrap: [AppComponent]
})
export class AppModule { }



